export function initBtn() {
   class Btn extends HTMLElement {
      constructor() {
         super();
         this.render();
      }
      render() {
         const shadow = this.attachShadow({ mode: "open" });
         const variant = this.getAttribute("btn");
         const clase = this.getAttribute("class");
         const div = document.createElement("div");
         const style = document.createElement("style");
         div.innerHTML = `
            <button>${variant}</button>
         `;
         // style.innerHTML = `
         //    button{
         //       width:100%;
         //       margin-top:2rem;
         //       border:none;
         //       background-color:#9CBBE9;
         //       height:40px;
         //       font-size:1.1rem;
         //       font-weight: 600;
         //    }
         //    .volver{
         //      color: #9CBBE9;
         //      bacground-color: #fff;
         //      border:solid 1px #000;
         //    }
         // `;
         if (clase === "comenzar") {
            console.log(clase);

            style.innerHTML = `
            .Comenzar{
                     width:100%;
                     margin-top:2rem;
                     border:none;
                     background-color:#9CBBE9;
                     height:40px;
                     font-size:1.1rem;
                     font-weight: 600;
                  }
            `;
         } else if (clase === "Volver") {
            console.log(clase);
            style.innerHTML = `
            .Volver{
                    color: #9CBBE9;
                    bacground-color: #fff;
                    border:solid 1px #000;
                  }
            `;
         }
         shadow.appendChild(style);
         shadow.appendChild(div);
      }
   }
   customElements.define("custom-btn", Btn);
}
